import * as React from 'react';
import * as ReactDOM from 'react-dom';

import App from './App';

const rootEl = document.getElementById('root');

ReactDOM.render(
  <App />,
  rootEl,
);

declare const module: any;
if (module.hot) {
  module.hot.accept('./App', () => {
    const NextApp = require('./App').default;
    ReactDOM.render(
      <NextApp />,
      rootEl,
    );
  });
}
